import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { ThermostatManagerComponent } from './thermostat-manager/thermostat-manager.component';
import { LightsManagerComponent } from './lights-manager/lights-manager.component';


const routes: Routes = [
  { path: 'lights', component: LightsManagerComponent },
  { path: 'thermostat', component: ThermostatManagerComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  declarations: [
    ThermostatManagerComponent,
    LightsManagerComponent
  ]
})
export class HomeManagerModule { }



