import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-thermostat-manager',
  templateUrl: './thermostat-manager.component.html',
  styleUrls: ['./thermostat-manager.component.css']
})
export class ThermostatManagerComponent implements OnInit {

  private currentTemp: number;
  private desiredTemp: number;

  constructor() { }

  ngOnInit() {
    this.currentTemp = 78;
    this.desiredTemp = 70;
  }

  private incrementTemp() {
    this.desiredTemp++;
  }


  private getHeatingOrCoolingSymbol() {
    if (this.currentTemp > this.desiredTemp) {
      return 'fa-snowflake';
    } else if (this.currentTemp < this.desiredTemp) {
      return 'fa-fire';
    } else {
      return '';
    }
  }

}



